import React, { Component } from 'react'
import { storeProducts, detailProduct } from './data'
import { runInThisContext } from 'vm';
import { ssh } from 'node-forge';
const ProductContext = React.createContext();

class ProductProvider extends Component {
  state = {
    products: [],
    detailProduct: detailProduct,
    cart: [],
    modalOpen: false,
    modalProduct: detailProduct,
    cartSubTotal: 0,
    cartTotal: 0,
  };

  componentDidMount() {
    this.storeProducts();
  }

  storeProducts = () => {
    let products = [];
    storeProducts.forEach(it => {
      const singleItem = { ...it };
      products = [...products, singleItem];
    });
    this.setState(() => { return { products } });
  }

  getItem = id => this.state.products.find(it => it.id === id);

  handleDetail = id => {
    const product = this.getItem(id);
    this.setState(() => { return { detailProduct: product } });
  }

  addToCart = id => {
    let tempProducts = [...this.state.products];
    const index = tempProducts.indexOf(this.getItem(id));
    const product = tempProducts[index];
    product.inCart = true;
    product.count = 1;
    const price = product.price;
    product.total = price;
    this.setState(() => {
      return { products: tempProducts, cart: [...this.state.cart, product] };
    }, () => { this.addTotals() });
  }

  openModal = id => {
    const product = this.getItem(id);
    this.setState(() => {
      return { modalProduct: product, modalOpen: true }
    })
  }

  closeModal = () => {
    this.setState(() => { return { modalOpen: false } })
  }

  increment = id => {
    let tempCart = [...this.state.cart];
    const selectedProduct = tempCart.find(it => it.id === id);

    selectedProduct.count = selectedProduct.count + 1;
    selectedProduct.total = selectedProduct.count * selectedProduct.price;

    this.setState({ cart: [...tempCart] }, () => { this.addTotals() });
  }

  decrement = id => {
    let tempCart = [...this.state.cart];
    const selectedProduct = tempCart.find(it => it.id === id);

    selectedProduct.count = selectedProduct.count - 1;

    if (selectedProduct.count === 0) {
      this.removeItem(id);
    } else {
      selectedProduct.total = selectedProduct.count * selectedProduct.price;
      this.setState({ cart: [...tempCart] }, () => this.addTotals());
    }
  }

  removeItem = id => {
    console.log('remove item' + id);
    let tempProducts = [...this.state.products];
    let tempCart = [...this.state.cart];

    tempCart = tempCart.filter(it => it.id !== id);

    const index = tempProducts.indexOf(this.getItem(id));
    let removedProduct = tempProducts[index];
    removedProduct.inCart = false;
    removedProduct.count = 0;
    removedProduct.total = 0;

    this.setState(() => {
      return {
        cart: [...tempCart],
        products: [...tempProducts],
      }
    }, () => {
      this.addTotals();
    });
  }

  clearCart = () => {
    this.setState({ cart: [] }, () => {
      this.storeProducts();
      this.addTotals();
    });
  }
  addTotals = () => {
    let total = 0;
    this.state.cart.map(it => (total += it.total));
    this.setState({ cartTotal: total });
  }

  render() {
    return (
      <ProductContext.Provider value={{
        ...this.state,
        handleDetail: this.handleDetail,
        addToCart: this.addToCart,
        openModal: this.openModal,
        closeModal: this.closeModal,
        increment: this.increment,
        decrement: this.decrement,
        removeItem: this.removeItem,
        clearCart: this.clearCart,
      }}>
        {this.props.children}
      </ProductContext.Provider>
    )
  }
}

const ProductConsumer = ProductContext.Consumer;
export { ProductProvider, ProductConsumer }