import React, { Component } from 'react'
import { ProductConsumer } from '../context'
import { ButtonContainer } from './Button'
import { Link } from 'react-router-dom'


export default class Modal extends Component {
  render() {
    return (
      <ProductConsumer>
        {(value) => {
          const { modalOpen, closeModal } = value;
          const { img, title, price, } = value.modalProduct;

          if (!modalOpen) {
            return null
          } else {
            return (
              <div className="modal-container">
                <div className=" container">
                  <div className="row">
                    <div className="col-8 mx-auto col-md-6 col-lg-4 text-center text-capitalize modal-item">
                      <h5>item added to the cart</h5>
                      <img src={img} className="img-fluid" alt="product" />
                      <h5 style={{ marginTop: '10px' }}>{title}</h5>
                      <h5 style={{ marginBottom: '20px' }} className="text-muted">price: {price}$</h5>
                      <Link to="/home">
                        <div className="button-wrapper button-wrapper-black" onClick={() => closeModal()}>
                          Store
                        </div>
                      </Link>
                      <Link to="/cart">
                        <div className="button-wrapper button-wrapper-main" onClick={() => closeModal()}>
                          go to cart
                        </div>
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            )
          }
        }}
      </ProductConsumer >
    )
  }
}

